<template>
  <div class="sidebar">
    <div class="sidebar-content">
      <!-- 上传菜单项 -->
      <div class="sidebar-item" :class="{ active: activeMenu === 'upload' }" @click="navigateTo('upload')">
        <div class="icon-container">
          <img v-if="activeMenu === 'upload'" src="@/assets/icons/uploadWhite@2x-1.png" alt="上传" class="custom-icon"/>
          <img v-else src="@/assets/icons/uploadBlack@2x-1.png" alt="上传" class="custom-icon"/>
          <span>上传</span>
        </div>
      </div>

      <!-- 第一个分隔箭头 -->
      <div class="arrow-container">
        <img src="@/assets/icons/opposingArrowBlue@2x.png" alt="arrow" class="arrow-icon"/>
      </div>

      <!-- 标注菜单项 -->
      <el-tooltip
        :disabled="hasSampleImage"
        content="请先选择样本"
        placement="right"
        effect="dark"
        :show-after="300"
        :popper-options="{ modifiers: [{ name: 'computeStyles', options: { adaptive: false } }] }"
      >
        <div class="sidebar-item" 
          :class="{ active: activeMenu === 'list', disabled: !hasSampleImage }" 
          @click="navigateTo('annotation')">
          <div class="icon-container">
            <img v-if="activeMenu === 'list'" src="@/assets/icons/annotationWhite.png" alt="标注" class="custom-icon"/>
            <img v-else src="@/assets/icons/annotationBlack.png" alt="标注" class="custom-icon"/>
            <span>标注</span>
          </div>
        </div>
      </el-tooltip>

      <!-- 第二个分隔箭头 -->
      <div class="arrow-container">
        <img src="@/assets/icons/opposingArrowBlue@2x.png" alt="arrow" class="arrow-icon"/>
      </div>

      <!-- 训练菜单项 -->
      <el-tooltip
        :disabled="hasSampleImage"
        content="请先选择样本"
        placement="right"
        effect="dark"
        :show-after="300"
      >
        <div class="sidebar-item" 
          :class="{ active: activeMenu === 'training', disabled: !hasSampleImage }" 
          @click="navigateTo('training')">
          <div class="icon-container">
            <img v-if="activeMenu === 'training'" src="@/assets/icons/trainWhite.png" alt="训练" class="custom-icon"/>
            <img v-else src="@/assets/icons/trainBlack.png" alt="训练" class="custom-icon"/>
            <span>训练</span>
          </div>
        </div>
      </el-tooltip>

      <!-- 第三个分隔箭头 -->
      <div class="arrow-container">
        <img src="@/assets/icons/opposingArrowBlue@2x.png" alt="arrow" class="arrow-icon"/>
      </div>

      <!-- 模型管理菜单项 -->
      <el-tooltip
        :disabled="hasSampleImage"
        content="请先选择样本"
        placement="right"
        effect="dark"
        :show-after="300"
      >
        <div class="sidebar-item" 
          :class="{ active: activeMenu === 'model', disabled: !hasSampleImage }" 
          @click="navigateTo('model')">
          <div class="icon-container">
            <img v-if="activeMenu === 'model'" src="@/assets/icons/modelManageWhite.png" alt="模型管理" class="custom-icon-model"/>
            <img v-else src="@/assets/icons/modelManageBlack.png" alt="模型管理" class="custom-icon-model"/>
            <span>模型管理</span>
          </div>
        </div>
      </el-tooltip>
      
    </div>
  </div>
</template>

<script setup lang="ts">
import {computed} from 'vue';
import {useRoute, useRouter} from 'vue-router';
import { useProjectStore } from '@/stores/project';
import { ElLoading } from 'element-plus';

const projectStore = useProjectStore();
const currentProject = computed(() => projectStore.currentProject);
const hasSampleImage = computed(() => currentProject.value?.has_sample_image ?? true);

const router = useRouter();
const route = useRoute();

// 根据当前路由确定激活的菜单项
const activeMenu = computed(() => {
  const path = route.path;
  if (path.includes('/upload')) return 'upload';
  if (path.includes('/sample/list') || path.includes('/sample/annotation')) return 'list';
  if (path.includes('/training')) return 'training';
  if (path.includes('/model')) return 'model';
  if(path.includes('/analysis')){
    return 'model'
  }
  return 'list'; // 默认为标注列表
});

// 导航到对应路由
const navigateTo = async (menu: string) => {
  // 对于需要样本的功能，检查是否有样本图片
  const requiresSample = ['annotation', 'training', 'model'];
  if (requiresSample.includes(menu) && !hasSampleImage.value) {
    // 如果没有样本图片，不进行跳转（tooltip会显示提示信息）
    return;
  }

  // 创建loading实例
  const loading = ElLoading.service({
    lock: true,
    text: '跳转中...',
    background: 'rgba(0, 0, 0, 0.7)',
  });

  try {
    switch (menu) {
      case 'upload':
        await router.push('/sample/upload');
        break;
      case 'annotation':
        await router.push('/sample/list');
        break;
      case 'training':
        await router.push('/sample/training');
        break;
      case 'model':
        await router.push('/sample/model');
        break;
      default:
        await router.push('/');
    }
  } catch (error) {
    console.error('路由跳转失败:', error);
  } finally {
    // 确保loading被关闭
    loading.close();
  }
};
</script>

